<template lang="pug">
    form.existed__group
        .existed__group-points
            .form__item
                label.label__name Тип группы:
                select(v-model="editedGroup.type" :disabled='!editMode').existed__group-point
                    option(:value='editedGroup.type') {{editedGroup.type}}
                    option(value='Индивидуально' v-if="editedGroup.type!=='Индивидуально'") Индивидуально
                    option(value='Мини-группа' v-if="editedGroup.type!=='Минигруппа'") Мини-группа
            .form__item
                label.label__name Предмет:
                select(v-model="editedGroup.subject.id" :disabled='!editMode').existed__group-point
                    option(:value='subject.id' v-for='subject in departmentSubjects' :selected='subject.id!=editedGroup.subject.id') {{subject.name}}            
            .form__item
                label.label__name Класс:
                select(v-model="editedGroup.groupClass" :disabled='!editMode').existed__group-point
                    option(:value='item' v-for='item in 12' :selected='item!==group.groupClass') {{item}}
        .existed__group-points
            .form__item
                label.label__name Тип учебной программы:
                select(v-model="editedGroup.eduProgrammeType" :disabled='!editMode').existed__group-point
                    option(:value='item' v-for='item in programmeTypes') {{item}}
            .form__item
                label.label__name Преподаватель:
                select(@change='onChangeTeacher()' v-model="editedGroup.teacher.id" :disabled='!editMode').existed__group-point
                    option(:value='teacher.id' v-for='teacher in departmentTeachers' :selected='teacher.id!==editedGroup.teacher.id') {{teacher.lastName}} {{teacher.firstName}}
            .form__item
                label.label__name Язык обучения:
                select(v-model="editedGroup.language" :disabled='!editMode').existed__group-point
                    option(:value='editedGroup.language') {{editedGroup.language}}
                    option(value='Казахский' v-if="'Казахский'!==editedGroup.language") Казахский
                    option(value='Русский' v-if="'Русский'!==editedGroup.language") Русский 
        .existed__group-points
            existed-group-schedule-container(
                :group='group'
                :editMode='editMode'
            )
        .existed__group-buttons
            .existed__group-buttons-content
                button(@click.prevent='groupScholars()').existed__group-view
                    .view__scholars-button
                button(@click.prevent='groupAddScholar()').existed__group-add-scholar
                    .add__scholars-button
                        img(src='../../../assets/images/icons/add_scholar.svg')
                group-add-scholar(
                    v-if='groupAddScholarVisible'
                    v-on:saveScholar='saveScholar'
                    :group='group'
                    :scholars='scholars'
                    :groupClass='group.groupClass'
                )
            .buttons__existed-group
                button(@click.prevent='editMode=!editMode' v-if='!editMode').edit__existed-group-button Редактировать
                button(@click.prevent='deleteCurrentGroup()' v-if='!editMode').remove__existed-group-button Удалить
                button(@click.prevent='saveGroup()' v-if='editMode').save__existed-group-button Сохранить
                button(@click.prevent='resetClick()' v-if='editMode').reset__existed-group-button Отмена
        group-scholar-list(
            :group='group'
            v-if='groupScholarListVisible'
            v-on:removeItems='removeItems'
        )
</template> 
<script>
import { mapState, mapActions } from "vuex";
export default {  
    components: {
        existedGroupScheduleContainer: () => import("../../admin/groups/existed-group-schedule-container"),
        groupScholarList: () => import("../../admin/groups/group-scholar-list"),
        groupAddScholar: () => import("../../admin/groups/group-add-scholar")
    },
    data() {
        return {
            changed: false,
            editMode: false,
            groupScholarListVisible: false,
            groupAddScholarVisible: false,
            disabled: 0,
            editedGroup: {
                id: this.group.id,
                type: this.group.type,
                subject: {
                    id: this.group.subject.id
                },
                groupClass: this.group.groupClass,
                eduProgrammeType: this.group.eduProgrammeType,
                teacher: {
                    id: this.group.teacher.id
                },
                language: this.group.language,
                schedule: []
            },
            programmeTypes: [
                'основная',
                'ЕНТ',
                'подготовка в спец.школу',
                'подготовка к олимпиаде',
            ]
        }
    },
    created() {
        this.fetchDepartmentSubjects(this.group.department.id),
        this.fetchDepartmentTeachers(this.group.department.id),
        this.fetchTeacherSchedule(this.group.teacher.id),
        this.fetchScholars(),
        this.fetchDepartmentTeachers(this.department.id),
        this.fetchDepartmentSubjects(this.department.id)
    },
    computed: {
        ...mapState ("teacherSchedule", {
            teacherSchedule: state => state.teacherSchedule}),  
        ...mapState ("groupSchedule", {
            groupSchedule: state => state.groupSchedule}),
        ...mapState ("scholars", {
            scholars: state => state.scholars}),
        ...mapState ("groups", {
            groups: state => state.groups}),
        ...mapState ("departmentSubjects", {
            departmentSubjects: state => state.departmentSubjects}),
        ...mapState ("departmentTeachers", {
            departmentTeachers: state => state.departmentTeachers})
    },
    props: {
        group: Object,
        department: Object,
    },
    methods: {
        ...mapActions("teacherSchedule", ["fetchTeacherSchedule"]),
        ...mapActions("departmentSubjects", ["fetchDepartmentSubjects"]),
        ...mapActions("departmentTeachers", ["fetchDepartmentTeachers"]),
        ...mapActions("scholars", ["fetchScholars"]),
        ...mapActions("groups", ["deleteGroup", "editGroup"]),
        groupScholars(){
            this.groupAddScholarVisible=false
            this.groupScholarListVisible=!this.groupScholarListVisible
        },
        groupAddScholar() {
            this.groupScholarListVisible=false
            this.groupAddScholarVisible=!this.groupAddScholarVisible
        },
        saveScholar() {
            this.groupAddScholarVisible=!this.groupAddScholarVisible
        },
        removeItems() {
            this.groupScholarListVisible = false
        },
        deleteCurrentGroup() {
            this.deleteGroup(this.group.id)
            let buttons = document.getElementsByClassName('groups__item-expand')
            buttons.forEach(elem => {
                elem.disabled=false
            });
        },
        saveGroup(){
            this.editedGroup.schedule = this.groupSchedule.filter(ele => ele.scheduleTime!=0)
            this.editGroup(this.editedGroup)
            this.editMode=!this.editMode
        },
        onChangeTeacher() {
            this.changed = true
            this.fetchTeacherSchedule(this.editedGroup.teacher.id)
        },
        resetClick() {
            this.$emit('resetChanges')
        }
    }
}
</script>
